import React from 'react'
import { DayOfWeekContainer, Image, DayBorder } from './DayOfWeek'

import { useNavigate } from 'react-router-dom'

const DayOfWeek = ({ day, maxmin, weather, id, path }) => {
  const { icon } = weather[0]
  const navigate = useNavigate()
  const weatherIcon = require(`../../assets/icons/${icon}.png`)

  return (
    <DayBorder
      onClick={() => navigate(`/clima/week/${id}`)}
      display={path.slice(-1) == id ? 'solid' : 'none'}
    >
      <DayOfWeekContainer>
        <h1> {day} </h1>
        <Image alt="Weather Icon" src={weatherIcon} />
        <h3> {maxmin}</h3>
      </DayOfWeekContainer>
    </DayBorder>
  )
}

export default DayOfWeek
