import React from 'react'
import { useLocation, useNavigate } from 'react-router-dom'
import Today from '../../components/Today/Today.jsx'
import Week from '../../components/Week/Week.jsx'
import Loading from '../../components/Loading/Loading.jsx'

import { Location, ClimeContainer, ClimeBorder } from './Clime.js'

const Clime = ({ data }) => {
  const location = useLocation()
  const navigate = useNavigate()
  const { current, daily } = data
  const date = new Date()
  console.log(data)
  //verifica en que dia (recurso) esta
  const path = location.pathname
  const day = path.slice(-1)

  return data.lat === undefined ? (
    <Loading />
  ) : (
    <ClimeContainer>
      <ClimeBorder shadow>
        <Location onClick={() => navigate('/clime/')}>
          Buenos Aires, Argentina
        </Location>

        <Today data={path === '/clime/' ? current : daily[day]} />
        <Week data={daily} today={date} path={path} />
      </ClimeBorder>
    </ClimeContainer>
  )
}

export default Clime
